import { Button } from "@mui/material";
import { makeStyles } from "@mui/styles";
import { Box } from "@mui/system";
import React from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router";
import { DirectionForm } from "../../../components/Forms";

const RoutePanel = ({ formik, calculator }) => {
  const navigate = useNavigate();
  const { form, formControl } = useStyles();
  const { regions } = useSelector(({ account }) => account);

  return (
    <Box component="form" className={form}>
      <DirectionForm formik={formik} toutePanel />
      {!calculator && (
        <Button
          size="small"
          sx={{
            ml: 2,
          }}
          onClick={() => navigate("/dashboard/ordering")}
          color="primary"
          variant="contained"
          disabled={!formik.values.to || !formik.values.from}
        >
          <i className="ri-arrow-right-line" />
        </Button>
      )}
    </Box>
  );
};

export default RoutePanel;

const useStyles = makeStyles((theme) => ({
  form: {
    background: "#FFFFFF",
    boxShadow: "0px 6px 23px rgba(0, 0, 0, 0.2)",
    borderRadius: "9px",
    width: "100%",
    display: "flex",
    margin: "0 auto",
    padding: "20px",
    zIndex: 1,

    [theme.breakpoints.down("xl")]: { width: "81%" },

    [theme.breakpoints.down("md")]: {
      width: "100%",
    },
  },
}));
